<template>
  <div class="indexSet-content-parent">
    <!--左边菜单-->
    <div class="goods-menu">
      <div class="goods-menu-top">
        <img
          src="../../assets/img/ico-menu1.png"
          style="margin: 0px 0px 0px 15px;cursor: pointer"
          id="ico-menu1"
          tag="0&quot;"
        />编码族管理
      </div>
      <div class="goods-menu-content">
        <ul id="file" class="filetree treeview">
          <li class="last">
            <span class="folder">客户编码</span>
          </li>
        </ul>
        <div id="goods-code-manager"></div>
      </div>
    </div>
    <!--右边内容-->
    <div class="goods-content">
      <div class="goods-content-top">
        <div class="goods-content-top-search">
          <div class="top-search-pic">
            <input type="text" style="z-index:200" class="top-search-pic-input"/>
          </div>
        </div>
        <div class="goods-content-top-right">
          <div class="goods-content-top-right-pic">
            <router-link to="/kp-set">
              <div class="close-text-exit"></div>
            </router-link>
            <div class="close-notice1" onclick="notice()"></div>
            <div class="close-notice1" onclick="notice()"></div>
            <!--打印-->
            <div class="close-notice2" onclick="notice()"></div>
            <div class="close-text-delete" @click="delete_goods()" id="goods_delete" data-id></div>
            <!-- 修改的按钮 -->
            <div class="close-text-change" @click="change_goods" id="goods_change" data-id></div>
            <!-- 增加的按钮 -->
            <div class="close-text-add" @click="add_goods"></div>
          </div>
        </div>
      </div>
      <div class="goods-content-table">
        <table cellpadding="0px" cellspacing="0px" class="goods-table">
          <thead>
          <tr>
            <th>编码</th>
            <th>名称</th>
            <th>简码</th>
            <th>税号</th>
            <th>地址电话</th>
            <th>银行账号</th>
            <th>邮件地址</th>
            <th>备注</th>
          </tr>
          </thead>
          <tbody id="goods-table1">
          <tr
            v-for="(item, i) in goods_table"
            :key="i"
            @click="select_goods(i)"
            :class="i===goodsNumber?'goods-active':''"
          >
            <td>{{item.customer_code}}</td>
            <td>{{item.customer_name}}</td>
            <td>{{item.customer_short_code}}</td>
            <td>{{item.customer_taxcode}}</td>
            <td>{{item.customer_forth_textarea}}</td>
            <td>{{item.customer_fifth_textarea}}</td>
            <td>{{item.customer_email}}</td>
            <td>{{item.remark}}</td>
          </tr>
          </tbody>
        </table>
      </div>
    </div>

    <!-- 这是添加客户的那个弹窗 -->
    <div id="add-customer-content" class="layui-layer-wrap" style="display: none">
      <div class="customer-save" @click="add_kehu_click"></div>
      <!--保存-->
      <div class="customer-cancel" @click="cancel_click"></div>
      <!--取消-->
      <div class="customer-first">
        <!--上级编码、客户名称-->
        <div class="customer-up-code">
          <!--上级编码，只能是数字-->
          <input
            type="text"
            class="pop-input"
            readonly
            id="customer-super-code"
            v-model="kehu_form.customer_up_code"
          />
        </div>
        <div class="customer-customer-name">
          <input id="customer_name" type="text" class="pop-input" v-model="kehu_form.customer_name"/>
        </div>
      </div>
      <div class="customer-second">
        <!--编码、客户税号-->
        <div class="customer-code">
          <!--只能是数字，最大六位-->
