        <!--银行账号-->
        <textarea
          class="customer-forth-textarea"
          id="bank"
          v-model="kehu_form.customer_fifth_textarea"
        ></textarea>
      </div>
      <div class="customer-sixth">
        <input type="text" id="remark" class="pop-input2" v-model="kehu_form.remark"/>
      </div>
      <!--备注-->
    </div>
